*{
  cursor: none;
}


.cursor-dot,
.cursor-dot-outline{
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 50%;
  opacity: 1;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}


.cursor-dot{
  width: 14px;
  height: 14px;
  background: transparent;
  border: 1px solid #CFE4FC;
}

.cursor-dot-outline{
  width: 48px;
  height: 48px;
  background: transparent;
  border: 1px solid #CFE4FC;
}



.movable {
  width: 40px;
  height: 40px;
  background: gold;
  position: absolute;
  z-index: 999999999;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  pointer-events: none;
  transition: background 0.5s;
}

.movable.active {
  background: red;
}

.movable.active:after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.movable.cursor-pointer:after {
  content: "!";
}

.movable.cursor-right:after {
  content: ">";
}

.movable.cursor-left:after {
  content: "<";
}

.sides {
  position: relative;
  background: lightblue;
}

.side-right,
.side-left {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 50%;
  background: transparent;
  border: 0;
  appearance: none;
}

.side-right {
  right: 0;
}
.side-left {
  left: 0;
}
